/*-----------------------------------------------------------------------------------------------------
    [page]
-------------------------------------------------------------------------------------------------------*/
/* 
*::selection { 
  color: $text-color;
  background : $primary; 
}
*/
html {
  scroll-behavior: smooth;
}

body {
  font-family: Arial, sans-serif;
  color: #000000;
  background: #FFFFFF;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

/* heading */
h1, h2, h3, h4, h5, h6 {
  font-family: Arial, sans-serif;
  color: #000000;
}

/* link */
a {
  text-decoration: none;
  color: #000000;
  transition: 100ms;
}

a:hover {
  color: #48466d;
  text-decoration: underline;
}

/* list */
ul,
ol {
  margin: 0;
}

ul li,
ol li {
  margin: 0;
}

/* img */
img {
  display: inline-block;
  max-width: 100%;
  font-style: italic;
}

/* blockquote */
blockquote {
  color: #000000;
  font-style: italic;
  font-family: Georgia, "Times New Roman", serif;
  font-size: 18px;
  line-height: 170%;
}

blockquote p:before {
  content: " “ ";
  color: #f1f1f1;
}

blockquote p:after {
  content: " ” ";
  color: #f1f1f1;
}

/* hr */
hr {
  background: #dddddd;
  border: none;
  height: 1px;
}

/* table */
table {
  border-collapse: collapse;
  border-spacing: 0;
}

table th {
  border: 1px solid gray;
  padding: 0.2em;
  text-align: left;
}

table td {
  border: 1px solid gray;
  padding: 0.2em;
}

