/*
* Custom Radio & Checkbox
* Source : https://medium.com/claritydesignsystem/pure-css-accessible-checkboxes-and-radios-buttons-54063e759bb3;
*/
.c-radio,
.c-checkbox {
  position: relative;
}

.c-radio__input,
.c-checkbox__input {
  opacity: 0;
  position: absolute;
}

.c-radio__label,
.c-checkbox__label {
  position: relative;
  display: inline-block;
  padding-left: 32px;
  font-size: 1.6rem;
  font-family: Arial, sans-serif;
  color: #000000;
}

.c-checkbox__label:before,
.c-checkbox__label:after {
  position: absolute;
  content: "";
  display: inline-block;
}

.c-checkbox__label:before {
  height: 24px;
  width: 24px;
  border: 2px solid #dddddd;
  border-radius: 0.4rem;
  left: 0px;
  top: 0px;
}

.c-checkbox__label:after {
  height: 9px;
  width: 14px;
  border-left: 3px solid #48466d;
  border-bottom: 3px solid #48466d;
  transform: rotate(-45deg);
  left: 5px;
  top: 6px;
}

.c-checkbox__input + .c-checkbox__label:after {
  content: none;
}

.c-checkbox__input:checked + .c-checkbox__label:after {
  content: "";
}

.c-checkbox__input:checked + .c-checkbox__label:before {
  border-color: #48466d;
}

.c-checkbox__input:focus + .c-checkbox__label::before {
  box-shadow: 0 0 0px 2px #fff, 0 0 0px 4px rgba(0, 0, 0, 0.16);
}

/*
* Custom select
* Source : https://github.com/filamentgroup/select-css
*/
/* class applies to select element itself, not a wrapper element */
.c-select {
  display: block;
  font-size: 16px;
  font-weight: 400;
  color: #000000;
  line-height: 1.3;
  padding: 8px 15px;
  border-radius: 0.4rem;
  box-shadow: none;
  height: 56px;
  width: 100%;
  max-width: 480px;
  box-sizing: border-box;
  margin: 0;
  border: 2px solid #dddddd;
  -moz-appearance: none;
  -webkit-appearance: none;
  appearance: none;
  background-color: #fff;
  /* note: bg image below uses 2 urls. The first is an svg data uri for the arrow icon, and the second is the gradient. 
		for the icon, if you want to change the color, be sure to use `%23` instead of `#`, since it's a url. You can also swap in a different svg icon or an external image reference
	*/
  background-image: url("data:image/svg+xml;charset=US-ASCII,%3Csvg%20xmlns%3D%22http%3A%2F%2Fwww.w3.org%2F2000%2Fsvg%22%20width%3D%22292.4%22%20height%3D%22292.4%22%3E%3Cpath%20fill%3D%22%23b1b1b1%22%20d%3D%22M287%2069.4a17.6%2017.6%200%200%200-13-5.4H18.4c-5%200-9.3%201.8-12.9%205.4A17.6%2017.6%200%200%200%200%2082.2c0%205%201.8%209.3%205.4%2012.9l128%20127.9c3.6%203.6%207.8%205.4%2012.8%205.4s9.2-1.8%2012.8-5.4L287%2095c3.5-3.5%205.4-7.8%205.4-12.8%200-5-1.9-9.2-5.5-12.8z%22%2F%3E%3C%2Fsvg%3E");
  background-repeat: no-repeat, repeat;
  /* arrow icon position (1em from the right, 50% vertical) , then gradient position*/
  background-position: right .7em top 50%, 0 0;
  /* icon size, then gradient */
  background-size: .65em auto, 100%;
}

/* Hide arrow icon in IE browsers */
.c-select::-ms-expand {
  display: none;
}

/* Hover style */
.c-select:hover {
  border-color: #48466d;
}

/* Focus style */
.c-select:focus {
  border-color: #48466d;
  /* It'd be nice to use -webkit-focus-ring-color here but it doesn't work on box-shadow */
  color: #000000;
  outline: none;
}

/* Set options to normal weight */
.c-select option {
  font-weight: normal;
}

/* Support for rtl text, explicit support for Arabic and Hebrew */
*[dir="rtl"] .c-select, :root:lang(ar) .c-select, :root:lang(iw) .c-select {
  background-position: left .7em top 50%, 0 0;
  padding: .6em .8em .5em 1.4em;
}

